<template>
  <div :style="{ backgroundImage: `url('${loginBackground()}')`, backgroundPosition: `${loginBackgroundPosition()}` }" class="h-screen w-full bg-page page-login" id="page-login">
    <div class="top-bar-wrap bg-white">
      <div class="container mx-auto">
        <div class="flex justify-between top-bar py-3">
          <div></div>
          <div><router-link :to="{ name: 'ContactUs', params: {} }" class="text-dark">{{$t('CONTACT_US')}}</router-link></div>
        </div>
      </div>
    </div>
    <div class="container mx-auto login-parent-container">
      <div class="login-container">
        <nv-card class="bg-loginLogoBackgroundColor mb-45">
          <div slot="no-body">
            <div class="nv-row">
              <div class="nv-col sm:w-full self-center">
                <div id="loading-container" class="vs-con-loading__container p-8">
                  <div class="nv-card__title text-center">
                    <h2 class="mb-4">
                      <img class="m-auto" :style="{ width: `${logoWidth()}` }" :src="loginLogo()" alt="login">
                    </h2>
                    <h4>{{ $t('INTRO_MSG') }}</h4>
                  </div>
                  <div class="alert-container mt-base" v-if="alertActive">
                    <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="m-0">
                      <p>{{ alertMessage }}</p>
                    </vs-alert>
                  </div>
                  <form action="#" @submit.prevent="login" v-on:keyup.enter="login">
                    <vs-input ref="username"
                              name="username"
                              :label="$t('USERNAME')"
                              v-model="username"
                              autocomplete="off"
                              class="w-full mt-6 no-icon-border"/>

                    <div class="password-input-group-wrap">
                      <router-link class="forget-password" :to="{ name: 'ResetPassword', params: {username: username} }">{{ $t('FORGOT_PASSWORD') }}</router-link>
                      <div class="nv-row mb-4">
                        <div class="nv-col w-full mt-6">
                          <label class="vs-input--label">{{ $t('PASSWORD') }}</label>
                          <password class="w-full no-icon-border" icon="icon icon-lock" icon-pack="feather" v-model="password" placeholder="" :toggle="true" :badge="false" :showStrengthMeter="false" autocomplete="off"/>
                        </div>
                      </div>
                    </div>

                    <div class="flex flex-wrap justify-between mt-6">
                      <vs-button class="sm:w-full" @click="login">{{ $t('LOGIN') }}</vs-button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </nv-card>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import SecurityService from '@/services/nucleus/SecurityService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import AppBrandingMixin from '@/mixins/commons/AppBrandingMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import Password from 'vue-password-strength-meter'
import AppFooter from '@/components/app-footer/AppFooter'

export default {
  name: 'login',
  mixins: [AlertMixin, AppBrandingMixin, LoadingMixin],
  components: {
    Password,
    AppFooter
  },
  data () {
    return {
      username: this.$route.params.username,
      password: ''
    }
  },
  methods: {
    login () {
      let self = this
      this.startLoading('loading-container')
      SecurityService.sessionToken().then(function (response) {
        self.$store.dispatch('setSession', { sessionToken: response.data })
        SecurityService.loginUser(self.username, self.password).then(function (response) {
          self.stopLoading('loading-container')
          if (response.data.sessionId) {
            self.$store.dispatch('setUser', {
              userId: response.data.userId,
              username: response.data.username,
              userFullName: response.data.userFullName,
              userRoles: response.data.userGroupIds
            })
            self.$store.dispatch('setSession', {
              sessionId: response.data.sessionId,
              sessionValidity: response.data.sessionValidity,
              sessionToken: response.data.sessionToken
            })
            self.$store.dispatch('setLocale', {
              languageCode: response.data.languageLocale,
              rtl: response.data.languageRtl === 'Y'
            })
            self.$acl.change(response.data.userGroupIds || 'public')

            if (self.$acl.check('isPartiallyAuthenticated')) {
              self.$router.push({ name: 'LoginAuthentication' })
            } else {
              if (self.$store.state.requestPath) {
                let requestPath = self.$store.state.requestPath
                self.$store.dispatch('setRequestPath', { setRequestPath: null })
                self.$router.push({ path: requestPath })
              } else {
                self.$router.push({ name: 'Dashboard' })
              }
            }
          } else {
            self.showDangerAlert(response.data.errors)
          }
        }).catch(function (error) {
          self.stopLoading('loading-container')
          if (error.response.data.errors && error.response.data.errors.indexOf('PWDCHG') !== -1) {
            self.$router.push({ name: 'ChangeTemporaryPassword', params: { username: self.username, oldPassword: self.password } })
          } else {
            self.showDangerAlert(error.response.data.errors || self.$t('LOGIN_FAILED'))
          }
        })
      }).catch(function () {
        self.stopLoading('loading-container')
        self.showDangerAlert(self.$t('LOGIN_FAILED'))
      })
    }
  },
  mounted () {
    this.$refs.username.$el.childNodes[1].childNodes[0].focus()
    this.$store.dispatch('resetState')
    if (this.$route.params.msg) {
      this.showSuccessAlert(this.$route.params.msg)
    }
  }
}
</script>
